import { Component, OnInit, ViewEncapsulation, AfterViewInit, ElementRef, ViewChild, HostBinding} from '@angular/core';
import {FormsModule, ReactiveFormsModule} from '@angular/forms';
import {FormControl, Validators, FormGroup, ValidationErrors} from '@angular/forms';
import {HttpClientModule} from '@angular/common/http';
import {HttpClient, HttpHeaders} from '@angular/common/http';
import { Helpers } from '../../../../helpers';
import { ScriptLoaderService } from '../../../../_services/script-loader.service';
import { FLY_IN_OUT_ANIMATION }   from '../../../../animations/fly-in-out.animation';
import { ROUND_ANTICLOCK_ANIMATION }   from '../../../../animations/round-anticlock.animation';
import { Router, Event, NavigationStart, NavigationEnd, ActivatedRoute } from '@angular/router';
import { ToastComponent } from '../../../layouts/toast-notification/toast.component';
import {ListComponent, SafeHtmlPipe} from '../../list/list.component';

import 'rxjs/add/operator/filter' ;

const EMAIL_REGEX = /^[a-zA-Z0-9.!#$%&’*+/=?^_`{|}~-]+@[a-zA-Z0-9-]+(?:\.[a-zA-Z0-9-]+)*$/;
const MOBILE_REGEX=/^[91]{2}-\d{10}$/;

@Component({
    selector: ".m-grid__item.m-grid__item--fluid.m-wrapper",
    templateUrl: "./location-add.component.html",
    styleUrls: ['./location-add.component.css'],
    encapsulation: ViewEncapsulation.None,
	animations: [ 
    FLY_IN_OUT_ANIMATION, ROUND_ANTICLOCK_ANIMATION
  ],
})

export class LocationAddComponent  {
    // @ViewChild(MatSort) sort: MatSort; 
	 @ViewChild(ListComponent)
	 private list: ListComponent;
	 @ViewChild(ToastComponent) toast: ToastComponent;
     status;
	  listHide: boolean = true;
	addHide: boolean = false;
	animateAdd: string = 'out';
	animateList: string = 'in';
	locationForm: FormGroup;
	passwordFormControl = new FormControl('', [ Validators.required]);
	 
	locationId = 0;  
	pageValue: string ="1";
	sortField: string ="";
	sortDirection: string ="";
	length = 10;
    pageSize = 5;
    pageSizeOptions = [5, 10, 25, 100];
	pageIndex: number=1;
	ajaxData : LocationData[];
	location = new LocationData();
	configDetails = new ConfigDetails();
    configInventoryLocationName='';
	  public Status =""; 
	  public branchs = [];
	 	public city = [
		{key: '1', name: 'Bangalore'},
		{key: '2', name: 'Mysore'},
		{key: '3', name: 'Kerala'}
	];
	  public msg = [];
    constructor(private _script: ScriptLoaderService, private router: Router,private route:ActivatedRoute,	private http: HttpClient,) {
		this.getConfigDetails();
		this.route.queryParams.filter(params => params.locationId).subscribe(params=>{
				if(params.locationId != undefined){
					this.locationId = params.locationId; 
				}
			});
		if(this.locationId == 0)
			{
			this.populateDropDownData();
			this.status = "Add";
			}
		else
		{
			this.status = "Update";
			this.populateData();
			this.listHide= true;
			
		}
    }
	  

ngOnInit() {
	
	this.locationForm = new FormGroup({			
			'locationName' : new FormControl('',[ Validators.required] ),
			'locationCode' : new FormControl('',[ Validators.required] ), 
			'locationBranchId' : new FormControl('',[ Validators.required] ),//[ Validators.required]
			'locationMobile1' : new FormControl('', [Validators.pattern(MOBILE_REGEX)]),//[ Validators.required, Validators.pattern(MOBILE_REGEX)]
			'locationMobile2' : new FormControl('', ),
			'locationPhone1' : new FormControl('', ),
			'locationPhone2' : new FormControl('', ),
			'locationPin' : new FormControl('',[ Validators.required]),
			'locationAddress' : new FormControl('', [ Validators.required]),
			'locationCityId' : new FormControl('',[ Validators.required] )
			
		});
}
	  
	  get locationName(){ return this.locationForm.get('locationName')};
	  get locationCode(){ return this.locationForm.get('locationCode')};
	  get locationBranchId(){ return this.locationForm.get('locationBranchId')};
	  get locationAddress(){ return this.locationForm.get('locationAddress')};
	  get locationCityId(){ return this.locationForm.get('locationCityId')};
	  get locationPin(){ return this.locationForm.get('locationPin')}; 
	  get locationMobile1(){ return this.locationForm.get('locationMobile1')};
	  get locationMobile2(){ return this.locationForm.get('locationMobile2')};
	  get locationPhone1(){ return this.locationForm.get('locationPhone1')};
	  get locationPhone2(){ return this.locationForm.get('locationPhone2')};
 
 
 populateDropDownData(){
	   const headers = new HttpHeaders({'Content-Type':'application/json'});
			   this.http.post<any[]>(sessionStorage.getItem('requestUrl')+'/axelacrm/customer-populate/branch',
							 			JSON.stringify({ emp_id :'0'}),
									{ headers, withCredentials: true }).subscribe(data =>{
				  														this.branchs = data['populateBranch']
			  															});
		  this.http.post<any[]>(sessionStorage.getItem('requestUrl')+'/axelacrm/customer-populate/configdetails',{},
									{ headers, withCredentials: true }).subscribe(data =>{
				  														this.configDetails = data['populateconfigdetails'][0];
				  														console.log(this.configDetails);
			  															});
	 
 }
	  getConfigDetails(){
		  const headers = new HttpHeaders({'Content-Type':'application/json'});
			  this.http.post<LocationData>(sessionStorage.getItem('requestUrl')+'/axelacrm/customer-populate/configdetails',
							 			JSON.stringify({}),
									{ headers, withCredentials: true }).subscribe(data =>{
				  													this.configDetails = data['populateconfigdetails'][0];
                                                                    console.log(this.configDetails);
			  															},
															 (err) => {
																	console.log("ERROR: "+err);
															 },
															 () => {
															 });
		  
	  }
	  populateData(){
		  const headers = new HttpHeaders({'Content-Type':'application/json'});
			   this.http.post<LocationData>(sessionStorage.getItem('requestUrl')+'/axelacrm/inventory/location-data',
							 			JSON.stringify({ locationId :this.locationId}),
									{ headers, withCredentials: true }).subscribe(data =>{
				  														this.location = data['populateData'][0];
																		console.log(this.location);
			  															},
															 (err) => {
																	/* this function is executed when there's an ERROR */
																	console.log("ERROR: "+err);
															 },
															 () => {
			  														this.populateDropDownData();								
															 });
 }
 toggleAnimation(){
	 this.router.navigate(['/Inventory/locationlist']);
 }
 onSubmit() {
	  if(!this.locationForm.invalid){
			  const headers = new HttpHeaders({'Content-Type':'application/json'});
 		if(this.locationId == 0){
			this.location['requestType'] = 'add';
		}else{
			this.location['requestType'] = 'update';
			this.location['locationId'] = this.locationId+'';
		}
		 this.http.post(sessionStorage.getItem('requestUrl')+'/axelacrm/inventory/location',
									JSON.stringify(this.location),
						{ headers, withCredentials: true }).subscribe(data => {
//			                                            this.toast.showtoast(this.msg);										
//			                                            this.toggleAnimation();
//			 											this.locationForm.reset();
//			 											this.list.getData();
//														});
			  
			  if(data['successmsg'] == undefined){
														this.msg[0]='Error';
			 											this.msg[1]=data['errormsg'];
														}else{
														if(data['successmsg'].includes('Successfully')){
														this.msg[0]='success';
			 											this.msg[1]=data['successmsg'];
														
														}
														}
			  											this.toast.showtoast(this.msg);
			  											if(this.msg[0]!='Error'){
														this.toggleAnimation();
			 											this.locationForm.reset();
														//this.list.getData();
														}
														},
														err =>{
			 												console.log(err['error']['desc']);
			 												this.msg[0]='Error';
			 												this.msg[1]=err['error'];
			 												this.toast.showtoast(this.msg);
		 												});
			  
		  }
	 
 }
  onDelete(){
	   this.location['requestType'] = 'delete';
		 this.location['locationId'] = this.locationId+'';
		 const headers = new HttpHeaders({'Content-Type':'application/json'});
		 this.http.post(sessionStorage.getItem('requestUrl')+'/axelacrm/inventory/location',
									JSON.stringify(this.location),
						{ headers, withCredentials: true }).subscribe(data => { 
		                           if(data['msg'].includes('Successfully')){
															this.msg[0]='success';
				 											this.msg[1]=data['msg'];
															}else{
															this.msg[0]='Error';
				 											this.msg[1]=data['msg'];
															}
				 											this.toast.showtoast(this.msg);
				 											this.toggleAnimation();
				 											this.locationForm.reset();
		                                                  //   this.list.getData();
															},
															err =>{
				 												console.log(err['error']['desc']);
				 												this.msg[0]='Error';
				 												this.msg[1]=err['error'];
				 												this.toast.showtoast(this.msg);
			 												});
	  
  }
}

export class LocationData { 
locationId: string;
locationBranchId: string;
locationName: string;
locationCode: string;
locationAddress: string;
locationCityId: string;
locationPin: string;
locationPhone1: string;
locationPhone2: string;
locationMobile1: string;
locationMobile2: string;
locationEntryId	: string;
	locationEntryBy	: string;
	locationEntryDate	: string;
locationModifiedId: string;  
	locationModifiedBy: string;  
 locationModifiedDate: string;  
}

export class ConfigDetails{
	configInventoryLocationName:string;
}



